export default {
  name: "Main",
  data() {
    return {
      logoSVG: logoSVG,
      scTimer: 0,
      scY: 0,
    };
  },
  computed: {
    GetIsLoading() {
      return this.$store.state.isLoading;
    },
    GetUpdateVersion() {
      return this.$store.state.updateVersions;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll: function () {
      if (this.scTimer) return;
      this.scTimer = setTimeout(() => {
        this.scY = window.scrollY;
        clearTimeout(this.scTimer);
        this.scTimer = 0;
      }, 100);
    },
    toTop: function () {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
